.shade-parent {
	position: absolute;
	display: flex;
	-webkit-box-align: center;
	align-items: center;
	-webkit-box-pack: center;
	justify-content: center;
	top: 0;
	bottom: 0;
	width: 100%;
	overflow-y: hidden;
	height: calc(100vh - 52px);
}

.shade-parent .shade {
	position: absolute;
	top: 68px;
	bottom: 0;
	background: var(--body-bg-shade);
	filter: blur(72px);
	border-radius: 24px;
	max-width: 480px;
	width: 100%;
	height: 100%;
}

.home-page {
	scroll-behavior: smooth;
	overflow-x: hidden;
	display: flex;
	flex-direction: column;
	width: 100%;
	min-height: 100vh;
	padding: 72px 0 0;
	align-items: center;
}

.home-page .card-wrapper {
	height: calc(100vh - 52px);
	width: 100%;
	display: flex;
	flex-direction: column;
	-webkit-box-align: center;
	align-items: center;
	z-index: 1;
}

.card-anchor {
	text-decoration: none;
	max-width: 480px;
	width: 100%;
}

.card-anchor .card-section {
	padding: 68px 8px 0;
	max-width: 480px;
	width: 100%;
}

.card-home-bg:hover {
	border: var(--card-hover-color);
	transform: translateY(-4px);
	transition: transform 250ms ease 0s;
}

#card-home-top {
	margin-bottom: 10px;
	color: var(--text-color);
	-webkit-box-pack: justify;
	justify-content: space-between;
	pointer-events: none;
}

.token-home {
	pointer-events: none;
}

/* Hero Content Wrapper */
.hero-content-wrapper {
	position: absolute;
	display: flex;
	flex-direction: column;
	-webkit-box-align: center;
	align-items: center;
	-webkit-box-pack: end;
	justify-content: flex-end;
	width: 100%;
	padding: 0 0 40px;
	max-width: min(720px, 90%);
	min-height: 500px;
	z-index: 990;
	transition: opacity 250ms ease 0s;
	height: calc(100vh - 124px);
	pointer-events: none;
}

.hero-content-wrapper .hero-title {
	color: transparent;
	font-size: 36px;
	line-height: 44px;
	font-weight: 700;
	text-align: center;
	margin: 0 0 24px;

	/* background: linear-gradient(20deg, rgb(255, 244, 207) 10%, rgb(255, 87, 218) 100%); */

	/* background: linear-gradient(20deg, #D697C1 10%, #FDC7C7 100%); */
	background: linear-gradient(93.06deg, #89cff0 2.66%, #088ecc 98.99%);
	background-clip: text;
	-webkit-text-fill-color: transparent;
}

.hero-text {
	display: flex;
	-webkit-box-pack: center;
	justify-content: center;
	pointer-events: auto;
}

.hero-text p {
	color: var(--text-color);
	font-size: 16px;
	line-height: 24px;
	font-weight: 500;
	text-align: center;
	max-width: 600px;
	margin: 0 0 32px;
}

.get-started-wrapper {
	max-width: 300px;
	width: 100%;
	pointer-events: auto;
}

#getStartedBtnId {
	text-decoration: none;
	max-width: 480px;
	width: 100%;
	padding: 16px 0;
	border-radius: 24px;
	pointer-events: auto;
}

.get-started-wrapper .get-started-btn {
	/* background: linear-gradient(93.06deg, rgb(255, 0, 199) 2.66%, rgb(255, 159, 251) 98.99%); */
	border: none;
	background: linear-gradient(93.06deg, #89cff0 2.66%, #088ecc 98.99%);
	color: rgb(255 255 255);
	transition: all 250ms ease 0s;
}

.get-started-btn.get-btn {
	padding: 16px;
	width: 100%;
	font-weight: 500;
	text-align: center;
	border-radius: 20px;
	outline: none;
	border: 1px solid transparent;
	color: rgb(255 255 255);
	text-decoration: none;
	display: flex;
	-webkit-box-pack: center;
	justify-content: center;
	flex-wrap: nowrap;
	-webkit-box-align: center;
	align-items: center;
	cursor: pointer;
	position: relative;
	z-index: 1;
	will-change: transform;
	transition: transform 450ms ease 0s;
	transform: perspective(1px) translateZ(0);
}

.get-started-btn .get-started-text {
	margin: 0;
	font-size: 16px;
	font-weight: 600;
	white-space: nowrap;
}

.learn-more {
	-webkit-box-align: center;
	align-items: center;
	color: var(--text-color);
	cursor: pointer;
	font-size: 20px;
	font-weight: 600;
	margin: 36px 0;
	display: flex;
	visibility: hidden;
	pointer-events: auto;
	transition: opacity 250ms ease 0s;
}

.learn-more .learn-more-icon {
	margin-left: 14px;
	size: 20px;
	border: 1px solid var(--text-color);
	border-radius: 50px;

	/* padding: 3px; */
	width: 25px;
	font-size: 15px;
	height: 25px;
	display: flex;
	align-items: center;
	justify-content: center;
}

.download-app {
	display: inline-flex;
	gap: 8px;
	color: var(--text-color);
	text-decoration: none;
	font-size: 16px;
	line-height: 25px;
	font-weight: 500;
	text-align: center;
	justify-content: center;
	align-items: center;
	pointer-events: auto;
}

.apple-icon {
	font-size: 24px;
}

/* Cards-Section */
.container-cards {
	display: flex;
	flex-direction: column;
	-webkit-box-align: center;
	align-items: center;
	padding: 0 24px 5rem;
	width: 100%;
	background: var(--cards-background);
}

.container-wrapper {
	display: grid;
	gap: 12px;
	width: 100%;
	padding: 24px 0 0;
	max-width: 1440px;
	scroll-margin: 72px 0 0;
	grid-template-columns: 1fr;
}

.container-wrapper .swap-tokens-card {
	display: flex;
	flex-direction: column;
	-webkit-box-pack: justify;
	justify-content: space-between;
	color: var(--open-currency-btn-top);
	height: 212px;
	box-shadow: rgb(51 53 72 / 4%) 0 10px 24px 0;

	/* background: url(/public/assets/images/cards/c-one.png) right center / auto 100% no-repeat border-box rgb(19, 26, 42); */
	background-position: right center;
	background-repeat: no-repeat;
	text-decoration: none;
	padding: 24px;
	border-radius: 24px;
	border-width: 1px;
	border-style: solid;
	border-color: transparent;
	border-image: initial;
	transition: border 250ms ease 0s;
}

.container-wrapper .card-one {
	background: url('/public/assets/images/cards/c-one.png') right center / auto 100% no-repeat
		border-box var(--home-cards);
}

.container-wrapper .card-two {
	background: url('/public/assets/images/cards/c-two.png') right center / auto 100% no-repeat
		border-box var(--home-cards);
}

.swap-tokens-card .trade-title {
	display: flex;
	-webkit-box-align: center;
	align-items: center;
	-webkit-box-pack: justify;
	justify-content: space-between;
}

.trade-title p {
	font-size: 20px;
	line-height: 28px;
	font-weight: 600;
}

.c-container-bottom {
	display: flex;
	flex-direction: column;
	font-size: 16px;
	line-height: 20px;
	color: var(--open-currency-btn-top);
	padding: 0 40px 0 0;
	max-width: 480px;
}

#cContainerLink {
	color: var(--btn-color);
	font-weight: 500;
	margin: 24px 0 0;
	cursor: pointer;
	transition: opacity 250ms ease 0s;
}

/* Small Cards */

.small-container-wrapper {
	display: grid;
	gap: 12px;
	width: 100%;
	padding: 24px 0 0;
	max-width: 1440px;
	scroll-margin: 72px 0 0;
	grid-template-columns: 1fr;
}

.small-container-wrapper a {
	display: flex;
	flex-direction: column;
	-webkit-box-pack: justify;
	justify-content: space-between;
	color: var(--open-currency-btn-top);
	height: 212px;
	box-shadow: rgb(51 53 72 / 4%) 0 10px 24px 0;
	background: right center / auto 100% no-repeat border-box var(--home-cards);
	background-position: right center;
	background-repeat: no-repeat;
	text-decoration: none;
	padding: 24px;
	border-radius: 24px;
	border-width: 1px;
	border-style: solid;
	border-color: rgb(152 161 192 / 24%);
	border-image: initial;
	transition: border 250ms ease 0s;
}

.crypto-guide {
	display: flex;
	-webkit-box-align: center;
	align-items: center;
	-webkit-box-pack: justify;
	justify-content: space-between;
}

.crypto-guide p {
	font-size: 20px;
	line-height: 28px;
	font-weight: 600;
}

.crypto-guide img {
	min-width: 20px;
	min-height: 20px;
	max-height: 48px;
	max-width: 48px;
}

.guide-text {
	display: flex;
	flex-direction: column;
	font-size: 16px;
	line-height: 20px;
	color: rgb(152 161 192);
	padding: 0 40px 0 0;
	max-width: 480px;
}

#btnLink {
	color: var(--btn-color);
	font-weight: 500;
	margin: 24px 0 0;
	cursor: pointer;
	transition: opacity 250ms ease 0s;
}

/* PROTOCOL-CARD */

.protocol-card {
	height: 340px;
	width: 100%;
	border-radius: 32px;
	max-width: 1440px;
	margin: 80px 0;
	display: flex;
	flex-direction: column;
	-webkit-box-align: center;
	align-items: center;
	-webkit-box-pack: justify;
	justify-content: space-between;
	padding: 32px 48px;
	box-shadow: rgb(51 53 72 / 4%) 0 10px 24px;
	background: url('data:image/png;base64,'),
		var(--protocol-card);
}

.protocol-card .protocol-left {
	color: var(--color-white);
	display: flex;
	flex: 1 1 0%;
	flex-direction: column;
}

.protocol-left .protocol-title {
	font-weight: 700;
	font-size: 28px;
	line-height: 36px;
	color: #fff;
}

.protocol-left .protocol-text {
	margin: 10px 10px 0 0;
	font-weight: 500;
	font-size: 16px;
	line-height: 20px;
	color: #fff;
}

.protocol-card .protocol-right {
	width: 100%;
	display: flex;
	-webkit-box-align: center;
	align-items: center;
	transition: opacity 250ms ease 0s;
}

.protocol-right .learnMoreBtn {
	background-color: transparent;
	color: rgb(76 130 251);
	display: flex;
	-webkit-box-pack: center;
	justify-content: center;
	-webkit-box-align: center;
	align-items: center;
}

.protocol-right .learn-more-btn {
	padding: 16px;
	width: 200px;
	font-weight: 500;
	text-align: center;
	border-radius: 20px;
	outline: none;
	border: 1px solid transparent;
	color: rgb(255 255 255);
	text-decoration: none;
	display: flex;
	-webkit-box-pack: center;
	justify-content: center;
	flex-wrap: nowrap;
	-webkit-box-align: center;
	align-items: center;
	cursor: pointer;
	position: relative;
	z-index: 1;
	will-change: transform;
	transition: transform 450ms ease 0s;
	transform: perspective(1px) translateZ(0);
}

.learn-more-btn.lmb {
	color: white;
	border: 1px solid white;
}

/* --------------------------------------------
--------------------------------------------
       MEDIA QUERY--Mobile
--------------------------------------------
-------------------------------------------- */
@media screen and (width >= 1280px) {
	/* Cards Section */
	.container-wrapper {
		/* padding: 32px; */
	}

	.c-container-bottom {
		font-size: 20px;
		line-height: 28px;
		max-width: 480px;
	}

	/* Small Cards */
	.small-container-wrapper a {
		padding: 32px;
	}

	.guide-text {
		font-size: 20px;
		line-height: 28px;
		max-width: 480px;
	}

	/* PROTOCOL-CARD */
	.protocol-left .protocol-title {
		font-size: 28px;
		line-height: 36px;
	}

	.protocol-left .protocol-text {
		font-size: 20px;
		line-height: 28px;
	}
}

@media screen and (width >= 1024px) {
	/* Cards Section */
	.container-wrapper {
		grid-template-columns: 1fr 1fr;
		gap: 32px;
	}

	.trade-title p {
		font-size: 28px;
		line-height: 36px;
	}

	/* Small Cards */
	.small-container-wrapper {
		grid-template-columns: 1fr 1fr 1fr;
		gap: 32px;
	}

	.crypto-guide p {
		font-size: 28px;
		line-height: 36px;
	}

	/* PROTOCOL-CARD */
	.protocol-card {
		height: 140px;
		flex-direction: row;
	}

	.protocol-card .protocol-right {
		width: auto;
	}
}

@media screen and (width >= 768px) {
	.hero-content-wrapper .hero-title {
		font-size: 64px;
		line-height: 72px;
	}

	.hero-text p {
		font-size: 20px;
		line-height: 28px;
	}

	.shade-parent {
		height: 100vh;
	}

	/* Cards Section */
	.container-cards {
		padding: 0 96px 5rem;
	}
}

@media screen and (width >= 640px) {
	.hero-content-wrapper .hero-title {
		font-size: 48px;
		line-height: 56px;
	}

	.get-started-btn .get-started-text {
		font-size: 20px;
	}

	.learn-more {
		visibility: visible;
	}

	/* Cards Section */
	.container-wrapper {
		grid-template-columns: 1fr 1fr;
		gap: 32px;
	}

	.container-wrapper .swap-tokens-card {
		height: 360px;
	}

	/* Small Cards */

	.small-container-wrapper {
		/* grid-template-columns: 1fr; */

		gap: 32px;
	}

	.small-container-wrapper a {
		height: 260px;
	}
}
