.position-item:hover {
	background-color: var(--open-currency-btn-bg);
}

.container-pool {
	display: flex;
	flex-direction: column;
	width: 100%;
	min-height: 100vh;
	padding: 72px 0 5rem;
	-webkit-box-align: center;
	align-items: center;
	flex: 1 1 0%;
}

.pools-container {
	padding: 68px 8px 0;
	max-width: 870px;
	width: 100%;
}

#poolsContainer {
	display: grid;
	grid-auto-rows: auto;
}

.pools-container .pool-wrapper {
	display: grid;
	grid-auto-rows: auto;
	row-gap: 24px;
	justify-items: center;
	margin-bottom: 1.4rem;
}

.pools-content {
	display: grid;
	grid-auto-rows: auto;
	row-gap: 24px;
}

.pools-head-content {
	width: 100%;
	display: flex;
	-webkit-box-align: center;
	align-items: center;
	-webkit-box-pack: start;
	justify-content: flex-start;
	padding: 0;
}

#poolsHead {
	-webkit-box-pack: justify;
	justify-content: space-between;
	color: var(--text-color);
}

.pools-left {
	box-sizing: border-box;
	margin: 0;
	min-width: 0;
	font-weight: 400;
	font-size: 36px;
	color: var(--color-white);
}

.pool-right {
	width: 100%;
	display: flex;
	padding: 0;
	-webkit-box-align: center;
	align-items: center;
	-webkit-box-pack: start;
	justify-content: flex-start;
}

.pool-right.right {
	width: fit-content;
	color: var(--text-color);
	margin: 0;
	min-width: 0;
	box-sizing: border-box;
}

.more-drop {
	display: flex;
	-webkit-box-pack: center;
	justify-content: center;
	-webkit-box-align: center;
	align-items: center;
	position: relative;
	border: none;
	text-align: left;
}

/* .knSXeI> :not(:last-child) {
    margin-left: 8px;
} */

#moreDrop {
	margin-left: 0;
}

#moreDrop .button {
	background-color: rgb(19 26 42);
	color: var(--text-color);
	font-size: 16px;
	font-weight: 500;
}

#moreBtn {
	/* padding: 16px; */
	padding: 6px 12px;
	width: 100%;
	font-weight: 500;
	text-align: center;
	border-radius: 20px;
	outline: none;
	border: 1px solid transparent;
	color: rgb(255 255 255);
	text-decoration: none;
	display: flex;
	-webkit-box-pack: center;
	justify-content: center;
	flex-wrap: nowrap;
	-webkit-box-align: center;
	align-items: center;
	cursor: pointer;
	position: relative;
	z-index: 1;
	will-change: transform;
	transition: transform 450ms ease 0s;
	transform: perspective(1px) translateZ(0);
}

.more-btn {
	border-radius: 12px;
	flex: 1 1 auto;
	padding: 6px 8px;
	width: 100%;
	background-color: rgb(13 17 28);
}

.btn-more {
	box-sizing: border-box;
	margin: 0;
	min-width: 0;
	appearance: none;
	margin-right: 8px;
	display: inline-block;
	text-align: center;
	line-height: inherit;
	text-decoration: none;
	font-size: inherit;
	padding: 8px 16px;
	color: white;
	border: 0;
	border-radius: 4px;
}

#moreText {
	-webkit-box-align: center;
	align-items: center;
	display: flex;
	color: rgb(255 255 255);
}

.more-text {
	box-sizing: border-box;
	margin: 0;
	min-width: 0;
	font-weight: 400;
	font-size: 16px;
}

