/* Connect WALLET CARD */
.pools-connect-wallet-card {
	background-color: var(--card-inside-color);
	border: 1px solid rgb(152 161 192 / 24%);
	border-radius: 16px;
	display: flex;
	flex-direction: column;
	box-shadow:
		rgb(0 0 0 / 1%) 0 0 1px,
		rgb(0 0 0 / 4%) 0 4px 8px,
		rgb(0 0 0 / 4%) 0 16px 24px,
		rgb(0 0 0 / 1%) 0 24px 32px;
	overflow: hidden;
	margin-bottom: 1.4rem;
}

.pools-connect-wallet-card-wrapper {
	-webkit-box-align: center;
	align-items: center;
	display: flex;
	flex-direction: column;
	-webkit-box-pack: center;
	justify-content: center;
	margin: auto;
	max-width: 300px;
	min-height: 25vh;
}

.pools-connect-wallet-card-wrapper .inbox {
	font-size: 45px;
