.apexcharts-pan-icon {
	transform: scale(0.62);
	position: relative;
	left: 1px;
	top: 0;
	display: none;
}

.apexcharts-menu-icon,
.apexcharts-reset-icon,
.apexcharts-zoom-icon {
	transform: scale(0.85);
	display: none;
}

.apexcharts-menu-icon,
.apexcharts-pan-icon,
.apexcharts-reset-icon,
.apexcharts-selection-icon,
.apexcharts-toolbar-custom-icon,
.apexcharts-zoom-icon,
.apexcharts-zoomin-icon,
.apexcharts-zoomout-icon {
	cursor: pointer;
	width: 40px;
	height: 30px;
	line-height: 24px;
	color: #6e8192;
	text-align: center;
}

.apexcharts-zoomin-icon,
.apexcharts-zoomout-icon {
	transform: scale(1.5);
}

.apexcharts-toolbar {
	display: none;
}
